var dirtyBPMN;
var confirmedBPMN;
window.loadComposition = (composition) => {
	jQuery.ajax({
			url:url+"/compositionbpmn/"+composition,
			method:"GET",
			dataType:"json",
			success: function(compo){
				console.log(compo);
				if(compo.dirty===true){
            			switch(compo.confirmed){

            				case -1: $("#dirty-dialog").modal("show");
            						modeler.importXML(compo.bpmn);
									dirtyBPMN=compo.dirtyBpmn;
									break;

							case  1:$("#dirty").css("display","inline");
									modeler.importXML(compo.dirtyBpmn);
									window.disableInteraction();
									$("#participantResponses").html(compo.responses);
									$("#compositionParticipants").html(compo.participants);
									if(compo.responses==compo.participants){
										$("#participantText").removeClass("grey-text");
										$("#participantText").addClass("blue-text");
										$("#confirm-dirty-dialog").modal();
									}
									break;

							case -2:$("#acceptanceScreen").css("display","inline");	
    								$("#bottom-area").css("display","none");
    								modeler.importXML(compo.dirtyBpmn);
									showMessage("Attention",
            								"A participant has reject the proposed adaptation. Please propose another or reject.");

									break;
            			/*	case 2: modeler.importXML(compo.dirtyBpmn);
            						confirmedBPMN=compo.bpmn;
            						$("#confirm-dirty-dialog").modal();
            						console.log(compo);*/
            						break;
            				case 0: modeler.importXML(compo.dirtyBpmn);
            						showMessage("Attention",
            								"Composition adaptation rejected by some participant. Adaptation will be discarded");
            						break;
            			}

				}else{
					modeler.importXML(compo.bpmn);
				}
				
				//modeler.on('element.click', removeHandler);
				sessionStorage.setItem("composition",composition);
				$("#compositions-dialog").modal('hide');
			},
			error:function(jx, status, error){
				console.log(error);
				showMessage("Error",error);
			}
		});
}

window.loadDirtyBPMN = () => {
	modeler.importXML(dirtyBPMN);
	$("#acceptanceScreen").css("display","inline");

    $("#bottom-area").css("display","none");
    $("#dirty-dialog").modal("hide");
}


window.acceptAdaptedModel = () => {
	$("#sending-loader").css("display","inline");
	modeler.saveXML({ format: true }, function(err, xml) {
		if (err) {
			showMessage("Error","Could not save BPMN 2.0 diagram");
			return console.error('Could not save BPMN 2.0 diagram', err);
		}
		var composition=sessionStorage.getItem("composition");

		jQuery.ajax({
			url:url+"/evolution/adaptation/"+composition,
			method:"POST",
			headers:{
				"Content-Type":"text/plain"
			},
			data: xml,
			success: function(numAffected){
				$("#sending-loader").css("display","none");
				$("#dirty").css("display","inline");
				$("#acceptanceScreen").css("display","none");
				$("#compositionParticipants").html(numAffected);
				showMessage("Attention","Adapted model sent to participants");
				window.disableInteraction();
			},
			error:function(jx, status, error){
				$("#sending-loader").css("display","none");
				console.log(error);
				showMessage("Error",error);
			}
		});
	});
}

var stopEventFlag=true;
window.disableInteraction = () => {
		var _this=this;
	    modeler.get("eventBus").on('drag.start', 1500, function(event) {
	      window.stopEvent(event);
	    });
	    modeler.get("eventBus").on('element.dblclick', 1400, function(event) {
	     window.stopEvent(event);
	    });
	    modeler.get("eventBus").on('element.click', 1400, function(event) {
	      window.stopEvent(event);
	    });
	}

window.stopEvent = (event) => {
  	if(stopEventFlag){
  	  event.preventDefault();
	  event.stopPropagation();
  	} 
}

window.rejectAdaptedModel = () => {
		$("#sending-loader").css("display","inline");
		var composition=sessionStorage.getItem("composition");
	
		jQuery.ajax({
			url:url+"/evolution/adaptation/"+composition,
			method:"DELETE",
			success: function(){
				$("#sending-loader").css("display","none");
				showMessage("Attention","The proposed adaptation has been rejected");
			},
			error:function(jx, status, error){
				$("#sending-loader").css("display","none");
				console.log(error);
				showMessage("Error",error);
			}
		});

}

window.confirmAdaptation = () => {
		$("#sending-loader").css("display","inline");
		/*modeler.importXML(confirmedBPMN);
		$("#sending-loader").css("display","none");
		$("#confirm-dirty-dialog").modal('hide');
		console.log(confirmedBPMN);*/

		var composition=sessionStorage.getItem("composition");
		jQuery.ajax({
			url:url+"/evolution/adaptation/"+composition,
			method:"PUT",
			success: function(bpmn){
				modeler.importXML(bpmn, (err, warnings) => {
	           	$("#dirty").css("display","none");
       			$("#bottom-area").css("display","inline");
       			$("#sending-loader").css("display","none");
       			$("#confirm-dirty-dialog").modal('hide');
	            stopEventFlag=false;
	        });
			},
			error:function(jx, status, error){
				$("#sending-loader").css("display","none");
				console.log(error);
				showMessage("Error",error);
			}
		});

}
